let app = require("@/config");
let enums = require("../roomApplication/enumsv1.js");
export default {
	onShow() {
		this.getData();
	},
	onLoad(opt) {
		this.labInfo.ID = opt.id;
		this.getData();
	},
	methods: {
		getData() {
			if (!this.labInfo.ID) return;
			uni.post("/api/building/GetRoom", {
				ID: this.labInfo.ID
			}, msg => {
				this.labInfo = msg.data;
				this.applicationData = msg.useState;
				if (this.labInfo.RoomType === 20) {
					uni.post("/api/building/GetSeats", {
						pid: this.labInfo.ID,
						page: 1,
						pageSize: 1000
					}, msg => {
						this.applicationData = msg.data;
					});
				}
			});
		},
		submit(id, description) {
			if (description === "按团队申请实验室") {
				uni.setStorage({
					key: 'labid',
					data: id,
					success: () => {
						uni.navigateTo({
							url: "../roomApplication/v2/roomFlowsCtrl?create=true"
						})
					}
				})
			} else {
				uni.setStorage({
					key: 'seatid',
					data: id,
					success: () => {
						uni.navigateTo({
							url: "../roomApplication/v2/seatFlowsCtrl?create=true"
						})
					}
				})
			}
		},
		release(seatID) {
			uni.post("/api/seatApp/v1/Release", {
				id: seatID
			}, msg => {
				if (msg.success) {
					this.getData();
				} else {
					uni.showMessage(msg.msg);
				}
			})
		},
		releaseRoom(roomID) {
			uni.post("/api/roomApp/v1/Release", {
				id: roomID
			}, msg => {
				if (msg.success) {
					this.getData();
				} else {
					uni.showMessage(msg.msg);
				}
			})
		}
	},
	data() {
		return {
			labInfo: {
				ID: "",
				Name: "",
				BuildingId: "",
				Building: "",
				administrator: "",
				AdminTelephone: "",
				SecurityOfficer: "",
				SOTelephone: "",
				displayOrder: "",
				CreatedOn: "",
				RoomType: ""
			},
			app,
			applicationData: [],
			labs: []
		}
	}
}
